@-webkit-keyframes'snowflake' {
  0 {-webkit-transform: translateY(0rem);transform: translateY(0rem);}
  80% {opacity: 0.9}
  100% {-webkit-transform: translateY(19rem);transform: translateY(19rem); opacity: 0}
}
@keyframes'snowflake' {
  0 {-webkit-transform: translateY(0rem);transform: translateY(0rem);}
  80% {opacity: 0.9}
  100% {-webkit-transform: translateY(19rem);transform: translateY(19rem); opacity: 0}
}
@-webkit-keyframes'jump' {
  0 {-webkit-transform: translateY(0rem);transform: translateY(0rem)}
  5% {-webkit-transform: translateY(-0.4rem);transform: translateY(-0.4rem)}
  10% {-webkit-transform: translateY(0rem);transform: translateY(0rem)}
  15% {-webkit-transform: translateY(-0.2rem);transform: translateY(-0.2rem)}
  20% {-webkit-transform: translateY(0rem);transform: translateY(0rem)}
}
@keyframes'jump' {
  0 {-webkit-transform: translateY(0rem);transform: translateY(0rem)}
  5% {-webkit-transform: translateY(-0.4rem);transform: translateY(-0.4rem)}
  10% {-webkit-transform: translateY(0rem);transform: translateY(0rem)}
  15% {-webkit-transform: translateY(-0.2rem);transform: translateY(-0.2rem)}
  20% {-webkit-transform: translateY(0rem);transform: translateY(0rem)}
}
@-webkit-keyframes'sport2' {
  0 {-webkit-transform: translateX(0px) scale(1);transform: translateX(0px) scale(1);}
  50% {-webkit-transform: translateX(10px) scale(1.1);transform: translateX(10px) scale(1.1); opacity: 0.6;}
}
@keyframes'sport2' {
  0 {-webkit-transform: translateX(0px) scale(1);transform: translateX(0px) scale(1);}
  50% {-webkit-transform: translateX(10px) scale(1.1);transform: translateX(10px) scale(1.1); opacity: 0.6;}
}
@-webkit-keyframes'sport3' {
  0 {-webkit-transform: rotateZ(0deg) scale(1);transform: rotateZ(0deg) scale(1);}
  50% {-webkit-transform: rotateZ(5deg) scale(1.05);transform: rotateZ(5deg) scale(1.05); opacity: 0.8;}
}
@keyframes'sport3' {
  0 {-webkit-transform: rotateZ(0deg) scale(1);transform: rotateZ(0deg) scale(1);}
  50% {-webkit-transform: rotateZ(5deg) scale(1.05);transform: rotateZ(5deg) scale(1.05); opacity: 0.8;}
}
@-webkit-keyframes'sport4' {
  0 {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
}
@keyframes'sport4' {
  0 {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
}
@-webkit-keyframes'sport5' {
